h3 {
	margin: 0;
}

.release {
	.modifier {
		padding: 10px 0;
	}

	.table-item {
		input {
			width: 100%;
			background-color: transparent;
			border: 0;
			text-align: center;
		}

		select {
			width: 100%;
			border: 1px solid #f1f2f7;
		}
	}
}

.title {
	padding-bottom: 5px;
	border-bottom: 1px solid $base-color;
	color: #555;
	font-size: 16px;
	font-weight: normal;
	text-indent: 5px;

	font {
		margin-right: 5px;
	}
}

.item-list {
	display: flex;
	align-items: center;

	> label {
		width: 90px;
		font-size: 14px;
	}
}

.input-short {
	width: 180px;
}

.input-default {
	box-sizing: border-box;
	height: 34px;
	padding: 0 7px;
	border: 1px solid #e2e4ec;
	border-radius: 3px;
}

.spotsalemap {
	width: 90%;
	height: 400px;
	border: 15px solid $base-color;
}

.laydate-box {
	border: 1px solid $base-color;

	input {
		width: 100px;
		padding: 7px 15px;
		margin: 0 5px;
		background-color: transparent;
		border: 0;
	}
}

.gift-money {
	padding: 10px 0;
	border-top: 1px solid $base-color;
	border-bottom: 1px solid $base-color;
	font-size: 14px;
}
