
aside {
	position: fixed;
	z-index: 161;
	top: 0;
	bottom: 0;
	left: 0;
	width: 230px;
	padding-top: 70px;
	transition: left 0.6s;

	&.active {
		left: -230px;
	}
}

nav {
	width: 100%;
	height: 100%;
	background-color: #2f394d;
	color: #c3d0db;
	font-size: 14px;

	.iconfont {
		margin-right: 16px;
		font-size: 22px;
		vertical-align: middle;
	}

	span {
		vertical-align: middle;
	}

	small {
		display: block;
		box-sizing: border-box;
		height: 32px;
		padding-left: 24px;
		border-top: 1px solid #39445a;
		border-bottom: 1px solid #39445a;
		color: #8994a1;
		line-height: 32px;
	}

	ul {
		margin: 0;
		padding: 0;

		a {
			display: block;
			padding-left: 24px;
			color: #c3d0db;
			text-decoration: none;
		}

		li {
			box-sizing: border-box;
			height: 55px;
			border-left: 4px solid transparent;
			line-height: 56px;
			transition: all 0.1s ease-in;
			cursor: pointer;

			&:hover {
				background-color: #2a3344;
			}
		}

		li.active {
			background-color: #202633;
			border-left: 4px solid #358de6;
			a {
				color: #358de6;
			}
		}
	}
}
