@charset 'utf-8';
@import './layout/header';
@import './layout/crumbs';
@import './layout/navbar';

body {
	background-color: $base-color;
}

ul {
	margin: 0;
}

li {
	list-style: none;
}

body {
	color: #797979;
	font: 12px/1.5 arial, 'Microsoft YaHei', tahoma, 'Hiragino Sans GB', \5b8b\4f53, sans-serif;
}

button,
input,
select,
textarea {
	outline: none;
}

.container {
	box-sizing: border-box;
	height: 100%;
	margin-left: 230px;
	padding: 85px 15px 15px;

	a {
		text-decoration: none;
	}
}

.box {
	padding: 15px;
	margin-bottom: 15px;
	background-color: #fff;
	border-radius: 3px;
}

.row {
	margin-left: -15px;
	margin-right: -15px;
}

.table-item {
	width: 100%;
	background-color: #fff;
	border-spacing: 0;
	border-collapse: collapse;

	th,
	td {
		box-sizing: content-box;
		padding: 10px;
		border-top: 1px solid #f1f2f7;
		text-align: center;
		vertical-align: middle;
		table-layout: fixed;
		cursor: pointer;
	}

	>thead th {
		border: 2px solid #f1f2f7;
		border-top: 0;
		border-right: 0;
		border-left: 0;
		font-size: 14px;
		font-weight: normal;
	}

	a:hover {
		color: #3a9bfc;
		text-decoration: underline;
	}

	tr:hover {
		background: lighten($base-color, 2%);
	}
}

.handle-item {
	display: flex;
	align-items: center;
	padding: 5px 45px;
	background-color: $base-color;

	li {
		min-width: 50px;
		margin-right: 5px;
	}

	span {
		display: block;
		text-align: center;
	}

	input,
	span {
		width: 100px;
		padding: 7px 15px;
		background-color: #fff;
		border: 1px solid rgba(150, 160, 180, 0.3);
		cursor: pointer;
	}

	.laydate {
		background-color: #fff;
		border: 1px solid rgba(150, 160, 180, 0.3);

		input {
			border: 0;
			margin: 0 5px;
	   	}
	}

	.active {
		color: #58c9f3;
	}
}

input::-webkit-placeholder {
	color: #000;
}

.release-addmarker {
	margin-left: 15px;
}
